@import '../../sass/variables';
@import '../../sass/mixins';

.overlay {
    position: fixed;
    @extend %flex-center;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    z-index: 100;
    background-color: $bgOverlay;
}


.spinner {
    @extend %flex-align-center;
	justify-content: space-evenly;
	width: $loaderSize;
	height: $loaderSize;

	.item {
		width: $loaderSize/10;
		height: 80%;
        border-radius: $loaderSize/20;
		background: $secondColor;
		animation: spinner1 1000ms ease-in-out infinite;

		@keyframes spinner1 {
			50% {
				height: 20%;
			}
		}
	}

	.item:nth-child(1) {
		animation-delay: calc(100ms * -3);
	}

    .item:nth-child(2) {
		animation-delay: calc(100ms * -1);
	}

    .item:nth-child(3) {
		animation-delay: calc(100ms * -2);
	}

    .item:nth-child(4) {
		animation-delay: calc(100ms * -1);
	}

    .item:nth-child(5) {
		animation-delay: calc(100ms * -3);
	}
}